import React from 'react';
import './TokensTable.css';
import DEFAULT_IMAGE from "../images/altcoin.png"

function TokensTable({ token }) {
    // const DEFAULT_IMAGE="../images/altcoin.png"
    return (
        <div className="TokenTable" >
            <img className="icon" src={token.logo_url} alt="" onError={(e) => { e.target.src = DEFAULT_IMAGE }} ></img>
            <p className="ticker">{token.contract_ticker_symbol}</p>
            <p className="coins">{Number((token.balance / (10 ** token.contract_decimals)).toFixed(5))}</p>
            <p className="balance">{"$ " + Number(token.quote.toFixed(3))}</p>
        </div>
    );
}


export default TokensTable;