import React, { useState } from "react";

export const DataContx = React.createContext();

export const DataProvider = ({ children }) => {
    const [address, setAddress] = useState(0);
    const [data, setData] = useState();
    const [trans, setTrans] = useState();
    const [nfts, setNfts] = useState();
    const [chain, setChain] = useState(250);
    return (
        <DataContx.Provider value={{
            address: address,
            setAddress: setAddress,
            chain: chain,
            setChain: setChain,
            data: data,
            setData: setData,
            trans: trans,
            setTrans: setTrans,
            nfts: nfts,
            setNfts: setNfts,
        }}>
            {children}
        </DataContx.Provider>
    );
}