import React, { useContext } from 'react';
import { DataContx } from '../services/DataContext';
import TokensTable from '../components/TokensTable';
import './Wallet.css';
import loader from "../images/loader.png"

function Wallet() {
  const contex = useContext(DataContx);
  return (
    <div>
      <div className='Main'>
        {contex.data == null &&
          <p>Please input adrress or check if address is correct</p>}
      </div>
      <div className='Main'>
        {
          !contex.data && contex.data === 0 &&
          <div>
            <img className="loader" src={loader}></img>
            <p className="loadertext">  loading...</p>
          </div>}
      </div>
      <div className='wallet'>
        {
          contex.data != 0 && contex.data && contex.data.items && contex.data.items.length > 0
          && contex.data.items.map((el, index) => (
            el.balance != '0' ?
              <TokensTable key={index} token={el} >
              </TokensTable> : ""))
        }
      </div>
    </div>
  );
}

export default Wallet;
