import React, { useContext, useState, useEffect } from 'react';
import './Amount.css';
import ftmImg from "../images/logo.svg"
import { DataContx } from '../services/DataContext';

function Amount() {
    const contex = useContext(DataContx);
    const [amt, setamt] = useState(0);
    const [amtUSD, setamtUSD] = useState(0);
    const [amtAll, setamtAll] = useState(0);

    useEffect(() => {
        if (contex.data && contex.data != 0 && contex.data != null) {
            setamt(0)
            setamtUSD(0)
            setamtAll(0);
            contex.data.items.forEach(element => {
                if (element.contract_ticker_symbol === "FTM") {
                    setamt(Number((element.balance / (10 ** element.contract_decimals)).toFixed(5)))
                    setamtUSD(Number(element.quote.toFixed(3)))
                }
                setamtAll(amtAll => amtAll + Number(element.quote.toFixed(3)))
            });
        } else {

        }
    }, [contex.data, contex.address]);
    return (
        contex.data !== null &&
        <div className="Amount">
            <img className="ftmImg" src={ftmImg}></img>
            <p className="amt">{amt} FTM</p>
            <p className="amtUSD">$ {amtUSD}</p>
            <p className="totalUSD"> Total: $ {Number(amtAll.toFixed(3))}</p>
        </div>
    )
}

export default Amount;