import React, { useContext } from 'react';
import { DataContx } from '../services/DataContext';
import Table from '../components/HistoryTable';
import './history.css';

function History() {
  const contex = useContext(DataContx);
  let mainAddr = 0;
  if (contex.trans && contex.trans.address) {
    mainAddr = contex.trans.address
  } else {

  }

  return (
    <div className='history'>
      {contex.data === null &&
        <p>Please input adrress or check if address is correct</p>}
      {

        contex.trans && contex.trans.items && contex.trans.items.length > 0 &&
        contex.trans.items.map((el, index) => (el.from_address === mainAddr || el.to_address === mainAddr) &&
          <Table key={index} transaction={el} adr={mainAddr} ></Table>,
        )

      }
    </div>
  );
}

export default History;
