import React from 'react';
import './NftTable.css';

function NftTable({ nftMeta }) {
    return (
        <div className="ntfTable">
            <img className="nftImage" src={nftMeta.image}></img>
            <div>
                <p className="nftTitle">{nftMeta.name ? nftMeta.name : "N/A"}</p>
                <p className="nftDesc">{nftMeta.description ? nftMeta.description : "N/A"}</p>
            </div>
        </div>
    )
}

export default NftTable;