import React, { useContext, useEffect, useState } from 'react';
import { DataContx } from '../services/DataContext';
import NftTable from '../components/NftTable';
import './Collectibles.css';

function Nft() {
  const contex = useContext(DataContx);
  const [nftsAll, setnftsAll] = useState([]);
  useEffect(() => {
    if (contex.nfts && contex.nfts.length > 0) {
      setnftsAll(contex.nfts)
    }
  }, [contex.nfts]);

  return (
    <div className='nft'>
      {
        nftsAll && nftsAll.length > 0 &&
        nftsAll.map((el, index) => <NftTable key={index} nftMeta={el} >

        </NftTable>)
      }
    </div>
  );
}

export default Nft;
