import React, { useState, useContext, useEffect } from 'react';
import './addressInput.css';
import * as FaIcons from 'react-icons/fa';
import { useCookies } from 'react-cookie';
import { DataContx } from '../services/DataContext'
import LOGO from "../images/logoblue.svg"


function AddressInput() {
    const [inputAddress, setinputAddress] = useState("");
    const contex = useContext(DataContx)
    const [cookies, setCookie] = useCookies(["address", "chain"]);
    const covAPIkey = 'ckey_46b722d0da164986af300bf574d'

    const changeAddress = () => {
        setCookie("address", inputAddress, { path: '/' })
        contex.setAddress(inputAddress)
        console.log("address changed", inputAddress)
    }

    const openFTMwebsite = () => {
        window.open("https://fantom.foundation/", "_blank")
    }
    useEffect(() => {
        setinputAddress(cookies.address)
        contex.setAddress(cookies.address)
    }, [cookies.address]);
    useEffect(() => {
        contex.setData(0)
        if (contex.address !== 0) {
            // get tokens
            (async () => {
                await fetch(`https://api.covalenthq.com/v1/250/address/${contex.address}/balances_v2/?key=${covAPIkey}`)
                    .then(res => res.json())
                    .then(res2 => {
                        contex.setData(res2.data)
                    })
            })();

        }
    }, [contex.address]);

    useEffect(() => {
        if (contex.address !== 0) {

            // get transactions
            (async () => {
                await fetch(`https://api.covalenthq.com/v1/250/address/${contex.address}/transactions_v2/?page-size=100&key=${covAPIkey}`)
                    .then(res => res.json())
                    .then(res2 => {
                        contex.setTrans(res2.data)
                    })
            })();

        }
    }, [contex.address]);

    useEffect(() => {
        if (contex.address !== 0) {
            // get nfts
            (async () => {
                let nftArr = []
                await fetch(`https://api.covalenthq.com/v1/250/address/${contex.address}/balances_v2/?nft=true&match=%7Btype%3A%22nft%22%7D&key=${covAPIkey}`)
                    .then(res => res.json())
                    .then(response => {
                        if (response.data
                            && response.data.items
                            && response.data.items.length > 0
                            && !response.error) {
                            response.data.items.forEach(el => {
                                el.nft_data.forEach(el2 => {
                                    let nftMeta = {
                                        id: el2.token_id,
                                        name: el2.external_data.name,
                                        description: el2.external_data.description,
                                        image: el2.external_data.image
                                    }
                                    nftArr.push(nftMeta)
                                }
                                )
                            });
                            contex.setNfts(nftArr)
                        } else {
                            contex.setNfts([])
                        }
                    })
            })();
        }
    }, [contex.address]);

    return (

        <div>
            <div className="inpt">
                <img
                    src={LOGO}
                    className="logo" onClick={openFTMwebsite}>
                </img>
                <input onChange={e => setinputAddress(e.target.value)} className="text" value={inputAddress}></input>
                <div
                    className="btn" onClick={changeAddress}>
                    <FaIcons.FaSearch />
                </div>
            </div>
            <hr className="solid"></hr>
        </div>
    );
}


export default AddressInput;