import React from 'react';
import './HistoryTable.css';

function Table({ transaction, adr }) {
	const openFTMwebsite = (tx_hash) => {
		window.open("https://ftmscan.com/tx/" + tx_hash, "_blank")
	}
	const openFTMwebsiteAddress = (addr) => {
		window.open("https://ftmscan.com/address/" + addr, "_blank")
	}
	return (
		<div className="table">
			<div className="details">
				<p className="left">Time/Date</p>
				<p className="right">{
					transaction.block_signed_at.slice(0, 10) + ' \n' + transaction.block_signed_at.slice(-9, -1) + " UTC"
				}</p>
			</div>
			<hr className="divide"></hr>
			<div className="details">
				<p className="left">Tx Hash</p>
				<a className="right link" onClick={() => openFTMwebsite(transaction.tx_hash)}>{
					transaction.tx_hash.slice(0, 8) + '...' + transaction.tx_hash.slice(-8)
				}</a>
			</div>
			<hr className="divide"></hr>
			<div className="details">
				<p className="left">Type</p>
				<p className="right" style={{ color: transaction.from_address === adr ? "#fcdc64" : "#0D88A8" }}>{
					transaction.from_address === adr ? "OUT" : "IN"
				}</p>
			</div>
			<hr className="divide"></hr>
			<div className="details">
				<p className="left">Status</p>
				<p className="right" style={{ color: transaction.successful ? "#358873" : "#DA3349" }}>{
					transaction.successful ? "Successful" : "Failed"
				}</p>
			</div>
			<hr className="divide"></hr>
			<div className="details">
				<p className="left">Sender</p>
				<a className="right link" onClick={() => openFTMwebsiteAddress(transaction.from_address)}>{
					transaction.from_address_label ? transaction.from_address_label : transaction.from_address.slice(0, 8)
						+ "..." + transaction.from_address.slice(-8)
					// transaction.to_address.slice(0,7)+'...'+transaction.from_address.slice(-7)
				}</a>
			</div>
			<hr className="divide"></hr>
			<div className="details">
				<p className="left">Receiver</p>
				<a className="right link" onClick={() => openFTMwebsiteAddress(transaction.to_address)}>{
					transaction.to_address_label ? transaction.to_address_label : transaction.to_address.slice(0, 8)
						+ "..." + transaction.to_address.slice(-8)
					// transaction.to_address.slice(0,7)+'...'+transaction.to_address.slice(-7)
				}</a>
			</div>
			<hr className="divide"></hr>
			<div className="details">
				<p className="left">Amount</p>
				<p className="right">{transaction.from_address === adr ? -Number((transaction.value / (10 ** 18)).toFixed(6)) : Number((transaction.value / (10 ** 18)).toFixed(6))

				} FTM</p>
			</div>

			{(transaction.from_address === adr) && (
				<div>
					<hr className="divide"></hr>
					<div className="details">
						<p className="left">Gas Used (Units)</p>
						<p className="right">{transaction.gas_spent}</p>
					</div>
					<hr className="divide"></hr>
					<div className="details">
						<p className="left">Gas Price (GWEI)</p>

						<p className="right">{
							Number((transaction.gas_price / (10 ** 9)).toFixed(0))
						}</p>
					</div>
				</div>)}

			{(transaction.from_address === adr) && (
				<div>
					<hr className="divide"></hr>
					<div className="details">
						<p className="left">Total</p>
						<p className="right">{
							// -Number((transaction.value_quote + transaction.gas_quote).toFixed(2)) + ` USD\n`  +
							-Number(((transaction.value / (10 ** 18)) + ((transaction.gas_price * transaction.gas_spent) / (10 ** 18))).toFixed(6)) + ` FTM`
						} </p>
					</div>
				</div>)}

		</div>
	);
}

export default Table;